'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { MouseEventHandler, ReactElement, Suspense, useMemo } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Logo } from '@yandex-music-int/sol/components/Logo';
import { LogoNew } from '@yandex-music-int/sol/components/LogoNew';
import { Navigation, NavigationGroup, NavigationItem } from '@yandex-music-int/sol/components/Navigation';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Tooltip, TooltipContent } from '@yandex-music-int/sol/components/Tooltip';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { UserProfile } from '@/entities/user/index.js';

import { WithClassName, routes, Route, useSelectedRoute, useStore, Experiments } from '@/shared/lib/index.js';
import { Link } from '@/shared/ui/index.js';

import { isNavigationItemVisible } from '../../../lib/isNavigationItemVisible.js';

import styles from './NavbarDesktop.module.scss';

interface Props extends WithClassName {
    collapsed: boolean;
    shownAnimation?: boolean;
    handleClick: MouseEventHandler<HTMLButtonElement>;
}

const flipOptions = {
    fallbackAxisSideDirection: 'start',
    padding: 8
} as const;

const shiftOptions = { padding: 8 };
const REWIND_IMAGE_URL = 'avatars.mds.yandex.net/get-music-misc/34161/img.6555f21067690e238fb12853/%%';

const getFeedbackUrl = (tld: string): string => {
    return `https://forms.yandex.${tld}/surveys/13483963.d2370c4d9063ff7a40bc0ddb26ad976fe135fa74`;
};

export const NavbarDesktop = observer(
    ({ className, collapsed, shownAnimation, handleClick }: Props): ReactElement<Props> => {
        const checkIsRouteSelected = useSelectedRoute();
        const { formatMessage } = useIntl();
        const { slides, experiments, settings } = useStore();

        const collapsedTooltipText = useMemo(() => {
            if (collapsed) {
                return formatMessage({ id: 'sidebar.uncollapse' });
            }
            return formatMessage({ id: 'sidebar.collapse' });
        }, [collapsed, formatMessage]);

        const navigationGroups = useMemo((): ReactElement[] => {
            const groups = [
                <NavigationGroup key="main">
                    {routes.filter(isNavigationItemVisible(experiments)).map((item: Route) => (
                        <Tooltip
                            key={item.messageId}
                            flipOptions={flipOptions}
                            shiftOptions={shiftOptions}
                            offsetOptions={-16}
                            placement="right"
                            enabled={collapsed}
                        >
                            <NavigationItem
                                tabIndex={1}
                                selected={checkIsRouteSelected(item.path)}
                                shownAnimation={shownAnimation}
                                variant="main"
                            >
                                <Link tabIndex={1} href={item.path}>
                                    <Icon
                                        variant={checkIsRouteSelected(item.path) ? `${item.icon}_selected` : item.icon}
                                        size="m"
                                    />
                                    <Caption
                                        variant="span"
                                        type="controls"
                                        size="m"
                                        weight="medium"
                                        lineClamp={1}
                                        className={clsx({
                                            [styles.title_animate]: shownAnimation,
                                            [styles.title_collapsed]: collapsed
                                        })}
                                    >
                                        <FormattedMessage id={item.messageId} />
                                    </Caption>
                                </Link>
                            </NavigationItem>
                            <TooltipContent>
                                <Caption variant="span" type="text" size="s" weight="medium">
                                    <FormattedMessage id={item.messageId} />
                                </Caption>
                            </TooltipContent>
                        </Tooltip>
                    ))}
                </NavigationGroup>
            ];

            if (experiments.checkExperiment(Experiments.WebRewind2023, 'on') && slides.userItems.length > 0) {
                groups.push(
                    <NavigationGroup key="rewind2023">
                        <Tooltip
                            flipOptions={flipOptions}
                            shiftOptions={shiftOptions}
                            offsetOptions={4}
                            placement="right"
                            enabled={collapsed}
                        >
                            <NavigationItem
                                tabIndex={1}
                                shownAnimation={shownAnimation}
                                className={styles.rewindItem}
                                withRipple
                            >
                                <Link tabIndex={1} href="/rewind2023">
                                    <Paper radius="s">
                                        <Image
                                            src={REWIND_IMAGE_URL}
                                            alt=""
                                            width={40}
                                            height={40}
                                            fit="contain"
                                            withAvatarReplace
                                            className={styles.rewindImage}
                                        />
                                    </Paper>
                                    <div
                                        className={clsx({
                                            [styles.title_animate]: shownAnimation,
                                            [styles.title_collapsed]: collapsed
                                        })}
                                    >
                                        <Caption
                                            variant="div"
                                            type="controls"
                                            size="m"
                                            weight="medium"
                                            className={styles.rewindTitle}
                                        >
                                            <FormattedMessage id="sidebar.rewind-title" />
                                        </Caption>
                                        <Caption variant="div" type="controls" size="s" weight="normal">
                                            <FormattedMessage id="sidebar.rewind-description" />
                                        </Caption>
                                    </div>
                                </Link>
                            </NavigationItem>
                            <TooltipContent>
                                <Caption variant="span" type="text" size="s" weight="medium">
                                    <FormattedMessage id="sidebar.rewind-title" />
                                </Caption>
                            </TooltipContent>
                        </Tooltip>
                    </NavigationGroup>
                );
            }

            groups.push(
                <NavigationGroup className={styles.feedbackGroup} key="feedback">
                    <Tooltip
                        flipOptions={flipOptions}
                        shiftOptions={shiftOptions}
                        offsetOptions={-16}
                        placement="right"
                        enabled={collapsed}
                    >
                        <NavigationItem variant="main" tabIndex={1} shownAnimation={shownAnimation}>
                            <Link tabIndex={1} href={getFeedbackUrl(settings.clientTld)}>
                                <Icon variant="feedback" size="m" />
                                <Caption
                                    variant="span"
                                    type="controls"
                                    size="m"
                                    weight="medium"
                                    lineClamp={1}
                                    className={clsx({
                                        [styles.title_animate]: shownAnimation,
                                        [styles.title_collapsed]: collapsed
                                    })}
                                >
                                    <FormattedMessage id="navigation.feedback" />
                                </Caption>
                            </Link>
                        </NavigationItem>
                        <TooltipContent>
                            <Caption variant="span" type="text" size="s" weight="medium">
                                <FormattedMessage id="navigation.feedback" />
                            </Caption>
                        </TooltipContent>
                    </Tooltip>
                </NavigationGroup>
            );

            return groups;
        }, [checkIsRouteSelected, collapsed, experiments, settings.clientTld, shownAnimation, slides.userItems.length]);

        const collapsedButton = useMemo(() => {
            return (
                <Tooltip
                    flipOptions={flipOptions}
                    shiftOptions={shiftOptions}
                    offsetOptions={8}
                    placement="right"
                    key="collapseTooltip"
                >
                    <Button
                        className={styles.collapseButton}
                        data-important
                        tabIndex={1}
                        aria-label={collapsedTooltipText}
                        radius="round"
                        color="secondary"
                        size="s"
                        icon={<Icon variant={collapsed ? 'uncollapse' : 'collapse'} size="xs" />}
                        onClick={handleClick}
                    />
                    <TooltipContent
                        className={clsx({
                            [styles.collapseButtonTooltip_hidden]: !collapsed
                        })}
                    >
                        {collapsedTooltipText}
                    </TooltipContent>
                </Tooltip>
            );
        }, [collapsed, handleClick, collapsedTooltipText]);

        const logo = useMemo(() => {
            if (experiments.checkExperiment(Experiments.webLogo2023, 'on2023')) {
                return (
                    <LogoNew
                        tabIndex={1}
                        className={clsx(styles.logo)}
                        collapsed={collapsed}
                        shownAnimation={shownAnimation}
                        collapsedButton={collapsedButton}
                    />
                );
            }
            return (
                <Logo
                    tabIndex={1}
                    className={clsx(styles.logo)}
                    collapsed={collapsed}
                    shownAnimation={shownAnimation}
                    collapsedButton={collapsedButton}
                />
            );
        }, [collapsed, collapsedButton, experiments, shownAnimation]);

        return (
            <div className={clsx(styles.root, className)}>
                <Link href="/" className={styles.logoLink}>
                    {logo}
                </Link>
                <Navigation
                    className={styles.navigation}
                    tabIndex={1}
                    collapsed={collapsed}
                    aria-label={formatMessage({ id: 'navigation.main-menu' })}
                >
                    {navigationGroups}
                </Navigation>

                <Suspense>
                    <div className={styles.user}>
                        <UserProfile
                            metaClassName={clsx(styles.userMeta, {
                                [styles.userMeta_animate]: shownAnimation,
                                [styles.userMeta_collapsed]: collapsed
                            })}
                            variant="desktop"
                            withMeta
                        />
                    </div>
                </Suspense>
            </div>
        );
    }
);
