'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useRef, Suspense } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import {
    CONTAINER_COOKIE_STORAGE,
    LayoutName,
    StorageKeys,
    WithChildren,
    WithClassName,
    useContainer,
    useLayout,
    useStore
} from '@/shared/lib/index.js';

import { useIsSecondary } from '../../lib/hooks/useIsSecondary.js';
import { Header } from '../Header/Header.js';

import styles from './Content.module.scss';

interface Props extends WithClassName, WithChildren {}

const CSS_TRANSITION_TIMEOUT_MS = 300;
const TRANSITION_STYLES = {
    enter: styles.secondaryEnter,
    enterActive: styles.secondaryEnterActive
};
export const Content = observer(({ className, children }: Props): ReactElement => {
    const container = useContainer();
    const { setContentRef } = useLayout();
    let isWebNextNavigationOn = false;

    if (process.env.BUILD_TYPE === 'web') {
        const cookieStorage = container.get(CONTAINER_COOKIE_STORAGE);
        isWebNextNavigationOn = cookieStorage.get(StorageKeys.WebNextNavigation) === 'on';
    }

    const { settings } = useStore();
    const isMobile = settings.layout === LayoutName.Mobile;

    const isSecondary = useIsSecondary();

    const articleRef = useRef<HTMLDivElement>(null);

    if (!isWebNextNavigationOn) {
        return (
            <main className={clsx(styles.rootOld, className)} ref={setContentRef}>
                <Suspense>{children}</Suspense>
            </main>
        );
    }

    return (
        <div className={clsx(styles.root, className)}>
            <main
                className={clsx(styles.primary, {
                    [styles.primary_short]: isSecondary && !isMobile,
                    [styles.primary_isMobile]: isMobile
                })}
            >
                {!isSecondary && children}
            </main>
            <TransitionGroup>
                {isSecondary && (
                    <CSSTransition
                        nodeRef={articleRef}
                        timeout={CSS_TRANSITION_TIMEOUT_MS}
                        classNames={TRANSITION_STYLES}
                        unmountOnExit
                    >
                        <article
                            className={clsx(styles.secondary, {
                                [styles.secondary_isMobile]: isMobile
                            })}
                            ref={articleRef}
                        >
                            <Header isMobile={isMobile} />
                            {children}
                        </article>
                    </CSSTransition>
                )}
            </TransitionGroup>
        </div>
    );
});
