'use client';

import { clsx } from 'clsx';
import { ReactElement, useCallback, useEffect, useState } from 'react';
import { useIntl } from 'react-intl';

import { Slider } from '@yandex-music-int/sol/components/Slider';

import { WithClassName, useSonata } from '@/shared/lib/index.js';

import { formatTimecode } from '../../lib/formatTimecode.js';

import styles from './ChangeTimecode.module.scss';
import { Timecode } from './Timecode/Timecode.js';

interface Props extends WithClassName {
    disabled?: boolean;
    isFullscreen: boolean;
    isMobile: boolean;
}

export const ChangeTimecode = ({ className, disabled, isFullscreen, isMobile }: Props): ReactElement => {
    const sonata = useSonata();
    const isTimecodeVisible = isFullscreen || !isMobile;
    const [isChangingTimecode, setIsChangingTimecode] = useState(false);
    const [position, setPosition] = useState(0);
    const [duration, setDuration] = useState(0);
    const [loaded, setLoaded] = useState(0);
    const { formatMessage } = useIntl();

    const onChange = useCallback(
        (newPosition: number, shouldApply?: boolean) => {
            setIsChangingTimecode(!shouldApply);
            if (shouldApply) {
                sonata?.setProgress(newPosition);
            } else {
                setPosition(newPosition);
            }
        },
        [sonata]
    );

    useEffect(() => {
        const unsubscribeFromProgressChange = sonata?.state.playerState.progress.onChange(() => {
            const newProgress = sonata.state.playerState.progress.value;

            setDuration(newProgress.duration);
            setPosition((currentPosition: number) => (isChangingTimecode ? currentPosition : newProgress.position));
            setLoaded(newProgress.loaded);
        });

        return () => {
            unsubscribeFromProgressChange?.();
        };
    }, [sonata, isChangingTimecode]);

    return (
        <div
            className={clsx(
                styles.root,
                {
                    [styles.root_fullscreen]: isFullscreen,
                    [styles.root_mobile]: isMobile
                },
                className
            )}
        >
            {isTimecodeVisible && (
                <Timecode
                    value={formatTimecode(Math.round(position), Math.round(duration))}
                    variant="start"
                    className={styles.timecode}
                />
            )}
            <Slider
                className={clsx(
                    styles.slider,
                    {
                        [styles[`slider_thumbSize_${!isFullscreen && !isMobile ? 'm' : 'xs'}`]]: true
                    },
                    className
                )}
                disabled={disabled}
                thumbSize={!isFullscreen && !isMobile ? 's' : 'xs'}
                trackSize={!isFullscreen && !isMobile ? 's' : 'xs'}
                value={Math.round(position)}
                mode="deferred"
                secondaryValue={Math.round(loaded)}
                maxValue={duration === 0 ? undefined : Math.round(duration)}
                onChange={onChange}
                aria-label={formatMessage({ id: 'player-actions.timecode-control' })}
            />
            {isTimecodeVisible && (
                <Timecode
                    value={formatTimecode(Math.round(duration), Math.round(duration))}
                    variant="end"
                    className={styles.timecode}
                />
            )}
        </div>
    );
};
