'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { useCallback, useState, useMemo, ReactElement, MouseEvent } from 'react';

import {
    useContainer,
    useStore,
    CONTAINER_STORAGE,
    WithClassName,
    LayoutName,
    StorageKeys
} from '@/shared/lib/index.js';

import styles from './Navbar.module.scss';
import { NavbarDesktop } from './NavbarDesktop/NavbarDesktop.js';
import { NavbarMobile } from './NavbarMobile/NavbarMobile.js';

interface Props extends WithClassName {}

export const Navbar = observer(({ className }: Props): ReactElement => {
    const container = useContainer();
    const storage = container.get(CONTAINER_STORAGE);

    const { settings } = useStore();
    const [collapsed, setCollapsed] = useState<boolean>(storage.get(StorageKeys.NavbarCollapsed) || false);
    const [shownAnimation, setShownAnimation] = useState<boolean>(false);

    const NavbarContainer = useMemo(() => {
        return settings.layout === LayoutName.Mobile ? NavbarMobile : NavbarDesktop;
    }, [settings.layout]);

    const handleClick = useCallback(
        (event: MouseEvent<HTMLButtonElement>) => {
            event.stopPropagation();
            event.preventDefault();
            storage.set(StorageKeys.NavbarCollapsed, !collapsed, {
                expires: 180
            });
            setCollapsed(!collapsed);
            setShownAnimation(true);
        },
        [collapsed, storage]
    );

    return (
        <aside
            className={clsx(
                styles.root,
                {
                    [styles.root_collapsed]: collapsed
                },
                className
            )}
        >
            <NavbarContainer handleClick={handleClick} collapsed={collapsed} shownAnimation={shownAnimation} />
        </aside>
    );
});
