import { Route } from '@/shared/lib/index.js';
import { ExperimentsModel } from '@/shared/models/Experiments.js';

export const isNavigationItemVisible =
    (experiments: ExperimentsModel) =>
    ({ disablingExperimentName }: Route): boolean => {
        if (disablingExperimentName && experiments.checkExperiment(disablingExperimentName, 'on')) {
            return false;
        }

        return true;
    };
