import { clsx } from 'clsx';
import { ReactElement } from 'react';

import { Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '@/shared/lib/index.js';

import styles from './Timecode.module.scss';

interface Props extends WithClassName {
    value: string;
    variant: 'start' | 'end';
}

export const Timecode = ({ value, variant, className }: Props): ReactElement | null => {
    return (
        <Caption
            className={clsx(styles.root, styles[`root_${variant}`], className)}
            variant="span"
            size="s"
            type="entity"
            weight="medium"
        >
            {value}
        </Caption>
    );
};
