'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { CSSProperties, ReactElement, useMemo } from 'react';
import { useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { ExplicitMark } from '@yandex-music-int/sol/components/ExplicitMark';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { ChangeTimecode } from '@/features/change-timecode/index.js';
import { ChangeVolume } from '@/features/change-volume/index.js';
import { SonataControls, MusicEntityMetaModel, useOnLikeClick, useOnDislikeClick } from '@/features/sonata/index.js';

import { SeparatedArtists } from '@/entities/artist/index.js';

import { useStore, WithClassName } from '@/shared/lib/index.js';
import { DislikeButton, FutureFeature, LikeButton } from '@/shared/ui/index.js';

import { getFilteredPlayerBarArtists } from '../../../lib/getFilteredPlayerBarArtists.js';

import styles from './PlayerBarDesktop.module.scss';

interface Props extends WithClassName {
    entityMeta: MusicEntityMetaModel | null;
}

export const PlayerBarDesktop = observer(({ className, entityMeta }: Props): ReactElement => {
    const { user } = useStore();
    const { formatMessage } = useIntl();

    const style = useMemo(() => {
        return {
            '--player-average-color-background': entityMeta?.hslAverageColor
        };
    }, [entityMeta?.hslAverageColor]);

    // Изменение entityMeta?.artists не отлавливается в useMemo из-за особенностей MST,
    // поэтому реагируем на изменение трека
    // eslint-disable-next-line react-hooks/exhaustive-deps
    const artists = useMemo(() => getFilteredPlayerBarArtists(entityMeta?.artists), [entityMeta?.id]);

    const toggleLike = useOnLikeClick(entityMeta);
    const toggleDislike = useOnDislikeClick(entityMeta);

    return (
        <section
            style={style as CSSProperties}
            className={clsx(
                styles.root,
                {
                    [styles.root_disabled]: !entityMeta
                },
                className
            )}
        >
            <div className={styles.info}>
                <div className={styles.infoCard}>
                    {entityMeta && (
                        <>
                            <Paper radius="s" className={styles.coverContainer}>
                                <Image
                                    className={styles.cover}
                                    src={entityMeta.coverUri}
                                    size={100}
                                    fit="cover"
                                    withAvatarReplace
                                />
                            </Paper>
                            <div className={styles.description}>
                                <div className={styles.titleContainer}>
                                    <Caption
                                        className={styles.titleLink}
                                        variant="div"
                                        lineClamp={1}
                                        type="entity"
                                        size="m"
                                        weight="medium"
                                    >
                                        {entityMeta.title}
                                    </Caption>
                                    {entityMeta.explicitMarkVariant && (
                                        <div className={styles.explicitMarkWrapper}>
                                            <ExplicitMark variant={entityMeta.explicitMarkVariant} />
                                        </div>
                                    )}
                                </div>

                                <SeparatedArtists
                                    className={styles.artists}
                                    lineClamp={1}
                                    artists={artists}
                                    linkClassName={styles.artistLink}
                                    withLink={!entityMeta?.isUGC}
                                />
                            </div>
                        </>
                    )}
                </div>
                <div className={styles.infoButtons}>
                    {entityMeta && user.isAuthorized && (
                        <>
                            <LikeButton isLiked={entityMeta.isLiked} onClick={toggleLike} iconSize="xs" />
                            <DislikeButton isDisliked={entityMeta.isDisliked} onClick={toggleDislike} iconSize="xs" />
                        </>
                    )}
                </div>
            </div>
            <div className={styles.sonata}>
                <SonataControls isMobile={false} entityMeta={entityMeta} />
                <ChangeTimecode
                    className={styles.timecodeSlider}
                    disabled={!entityMeta}
                    isMobile={false}
                    isFullscreen={false}
                />
            </div>
            <div className={styles.meta}>
                <FutureFeature title={formatMessage({ id: 'player-actions.lyrics' })}>
                    <Button
                        radius="round"
                        size="xxxs"
                        variant="text"
                        disabled
                        withRipple={false}
                        aria-label={formatMessage({ id: 'player-actions.lyrics' })}
                        icon={<Icon variant="lyrics" size="xs" />}
                    />
                </FutureFeature>
                <FutureFeature title={formatMessage({ id: 'player-actions.history' })}>
                    <Button
                        radius="round"
                        size="xxxs"
                        variant="text"
                        disabled
                        withRipple={false}
                        aria-label={formatMessage({ id: 'player-actions.history' })}
                        icon={<Icon variant="history" size="xs" />}
                    />
                </FutureFeature>
                <FutureFeature title={formatMessage({ id: 'player-actions.cast' })}>
                    <Button
                        radius="round"
                        size="xxxs"
                        variant="text"
                        disabled
                        withRipple={false}
                        aria-label={formatMessage({ id: 'player-actions.cast' })}
                        icon={<Icon variant="cast" size="xs" />}
                    />
                </FutureFeature>
                <ChangeVolume />
            </div>
        </section>
    );
});
