import { Instance, types } from 'mobx-state-tree';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { MusicEntityMeta as MusicEntityMetaResponse } from '@yandex-music-int/player-entities';
import { AudioPlayerStatus } from '@yandex-music-int/sonata';

import { musicEntityMetaResponseToModel } from '../lib/musicEntityMetaResponseToModel.js';

import { MusicEntityMeta } from './MusicEntityMeta.js';

export const SonataState = types
    .model('SonataState', {
        contextType: types.maybeNull(types.enumeration(Object.values(YaMusicContexts))),
        contextId: types.maybeNull(types.string),
        entityMeta: types.maybeNull(MusicEntityMeta),
        status: types.enumeration(Object.values(AudioPlayerStatus)),
        canMoveForward: types.boolean,
        canMoveBackward: types.boolean,
        volume: types.maybe(types.number)
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => ({
        setContextId: (contextId: string | number) => {
            self.contextId = String(contextId);
        },
        setContextType: (contextType: YaMusicContexts) => {
            self.contextType = contextType;
        },
        setEntityMeta: (entityMeta: MusicEntityMetaResponse | null) => {
            if (entityMeta) {
                self.entityMeta = musicEntityMetaResponseToModel(entityMeta);
            }
        },
        setStatus: (status: AudioPlayerStatus) => {
            self.status = status;
        },
        setCanMoveForward: (value: boolean) => {
            self.canMoveForward = value;
        },
        setCanMoveBackward: (value: boolean) => {
            self.canMoveBackward = value;
        },
        setVolume: (volume: number) => {
            self.volume = volume;
        }
    }));

export type SonataStateModel = Instance<typeof SonataState>;
