import { usePathname } from 'next/navigation.js';
import { useMemo } from 'react';

const SECONDARY_PAGES = ['album', 'users', 'artist', 'chart'];

export const useIsSecondary = (): boolean => {
    const pathname = usePathname();

    return useMemo(() => {
        const [, path] = pathname.split('/');
        if (!path) {
            return false;
        }
        return SECONDARY_PAGES.includes(path);
    }, [pathname]);
};
