import { cast } from 'mobx-state-tree';

import { MusicEntityMeta } from '@yandex-music-int/player-entities';
import { Artist as ArtistResponse } from '@yandex-music-int/resource-collection';

import { artistResponseToModel } from '@/entities/artist/index.js';
import { trackResponseToModel } from '@/entities/track/index.js';

import { MusicEntityMetaModel } from '../model/MusicEntityMeta.js';

export const musicEntityMetaResponseToModel = (trackWithArtistsResponse: MusicEntityMeta): MusicEntityMetaModel => {
    const artists = trackWithArtistsResponse.artists?.map((artist: ArtistResponse) => {
        return artistResponseToModel(artist);
    });

    return cast({
        ...trackResponseToModel(trackWithArtistsResponse),
        artists
    });
};
