'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
//@ts-expect-error
import Link from 'next/link';
import { ReactElement } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Navigation, NavigationGroup, NavigationItem } from '@yandex-music-int/sol/components/Navigation';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { UserProfile } from '@/entities/user/index.js';

import { WithClassName, routes, Route, useSelectedRoute, useStore } from '@/shared/lib/index.js';

import { isNavigationItemVisible } from '../../../lib/isNavigationItemVisible.js';

import styles from './NavbarMobile.module.scss';

interface Props extends WithClassName {}

export const NavbarMobile = observer(({ className }: Props): ReactElement<Props> => {
    const checkIsRouteSelected = useSelectedRoute();
    const { experiments } = useStore();
    const { formatMessage } = useIntl();
    const mainMenuLabel = formatMessage({ id: 'navigation.main-menu' });

    return (
        <div tabIndex={1} className={clsx(styles.root, className)}>
            <Navigation tabIndex={1} collapsed direction="horizontal" aria-label={mainMenuLabel}>
                <NavigationGroup>
                    <>
                        {routes.filter(isNavigationItemVisible(experiments)).map((item: Route) => (
                            <NavigationItem
                                tabIndex={1}
                                selected={checkIsRouteSelected(item.path)}
                                key={item.messageId}
                            >
                                <Link tabIndex={1} href={item.path}>
                                    <Icon variant={item.icon} size="m" />
                                    <Caption variant="span" type="controls" size="m" weight="medium" lineClamp={1}>
                                        <FormattedMessage id={item.messageId} />
                                    </Caption>
                                </Link>
                            </NavigationItem>
                        ))}

                        <NavigationItem>
                            <UserProfile className={styles.user} variant="mobile" />
                        </NavigationItem>
                    </>
                </NavigationGroup>
            </Navigation>
        </div>
    );
});
