export const formatTimecode = (currentSeconds: number, totalSeconds: number): string => {
    const hours = Math.floor(currentSeconds / 3600);
    const minutes = Math.floor((currentSeconds - hours * 3600) / 60);
    const seconds = currentSeconds - hours * 3600 - minutes * 60;

    const shouldShowHours = Math.floor(totalSeconds / 3600) > 0;

    const output = [minutes, seconds];

    if (shouldShowHours) {
        output.unshift(hours);
    }

    return output.map((value: number) => String(value).padStart(2, '0')).join(':');
};
