'use client';

import { ReactElement } from 'react';
import { useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';

import { FutureFeature } from '@/shared/ui/index.js';
import { PlayButton } from '@/shared/ui/PlayButton/PlayButton.js';

import styles from './SonataControlsDesktop.module.scss';

interface Props {
    disabled?: boolean;
    isPlaying: boolean;
    canMoveForward: boolean;
    canMoveBackward: boolean;
    onClickNext: () => void;
    onClickPrev: () => void;
    onClickPlayPause: () => void;
}

export const SonataControlsDesktop = ({
    disabled,
    isPlaying,
    canMoveForward,
    canMoveBackward,
    onClickNext,
    onClickPrev,
    onClickPlayPause
}: Props): ReactElement => {
    const { formatMessage } = useIntl();
    return (
        <div className={styles.root}>
            <FutureFeature title={formatMessage({ id: 'player-actions.shuffle' })}>
                <Button
                    radius="round"
                    size="xxxs"
                    variant="text"
                    disabled
                    withRipple={false}
                    aria-label={formatMessage({ id: 'player-actions.shuffle' })}
                    icon={<Icon variant="shuffle" size="xs" />}
                />
            </FutureFeature>

            <div className={styles.sonataButtons}>
                <Button
                    className={styles.sonataButton}
                    variant="text"
                    size="m"
                    radius="round"
                    disabled={!canMoveBackward}
                    withRipple={false}
                    aria-label={formatMessage({ id: 'player-actions.previous-track' })}
                    icon={<Icon variant="previous" />}
                    onClick={onClickPrev}
                />
                <PlayButton
                    className={styles.sonataButton}
                    disabled={disabled}
                    iconSize="l"
                    variant="filled"
                    isPlaying={isPlaying}
                    iconClassName={styles.playButtonIcon}
                    onClick={onClickPlayPause}
                />
                <Button
                    className={styles.sonataButton}
                    radius="round"
                    size="m"
                    variant="text"
                    disabled={!canMoveForward}
                    withRipple={false}
                    aria-label={formatMessage({ id: 'player-actions.next-track' })}
                    icon={<Icon variant="next" />}
                    onClick={onClickNext}
                />
            </div>
            <FutureFeature title={formatMessage({ id: 'player-actions.repeat' })}>
                <Button
                    radius="round"
                    size="xxxs"
                    variant="text"
                    disabled
                    withRipple={false}
                    aria-label={formatMessage({ id: 'player-actions.repeat' })}
                    icon={<Icon variant="repeat" size="xs" />}
                />
            </FutureFeature>
        </div>
    );
};
