'use client';

import { useCallback, useState } from 'react';
import { useIntl } from 'react-intl';

import { isVibeContext } from '@yandex-music-int/player-contexts';
import { RotorResourceSessionFeedback, UsersResourceLibraryResponse } from '@yandex-music-int/resource-collection';

import { NotificationLike } from '@/entities/track/index.js';

import { NotificationContainerId, useNotify, useSonata, useStore } from '@/shared/lib/index.js';
import { BaseNotificationError } from '@/shared/ui/index.js';

import { MusicEntityMetaModel } from '../model/MusicEntityMeta.js';

export const useOnLikeClick = (entityMeta: MusicEntityMetaModel | null): (() => Promise<void>) => {
    const { user } = useStore();
    const { notify } = useNotify();
    const [pendingLibraryAction, setPendingLibraryAction] = useState(false);
    const { formatMessage } = useIntl();

    const sonata = useSonata();

    return useCallback(async () => {
        if (entityMeta) {
            if (!user.isAuthorized) {
                notify(
                    <BaseNotificationError
                        error={formatMessage({ id: 'authorization-messages.need-to-authorizate' })}
                    />,
                    {
                        containerId: NotificationContainerId.ERROR
                    }
                );
                return;
            }

            if (pendingLibraryAction) {
                return;
            }

            setPendingLibraryAction(true);

            const result: UsersResourceLibraryResponse = await entityMeta.toggleLike();

            setPendingLibraryAction(false);

            if (result === UsersResourceLibraryResponse.OK) {
                notify(<NotificationLike track={entityMeta} />, { containerId: NotificationContainerId.INFO });

                const feedbackType = entityMeta.isLiked
                    ? RotorResourceSessionFeedback.LIKE
                    : RotorResourceSessionFeedback.UNLIKE;
                if (sonata && isVibeContext(sonata.state.currentContext.value)) {
                    sonata.state.currentContext.value.sendFeedback(
                        sonata.state.playerState,
                        sonata.state.queueState,
                        feedbackType
                    );
                }
            } else {
                notify(<BaseNotificationError error={formatMessage({ id: 'error-messages.error-during-action' })} />, {
                    containerId: NotificationContainerId.ERROR
                });
            }
        }
    }, [entityMeta, user.isAuthorized, pendingLibraryAction, notify, formatMessage, sonata]);
};
