'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useCallback } from 'react';
import { useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Slider } from '@yandex-music-int/sol/components/Slider';

import {
    CONTAINER_STORAGE,
    StorageKeys,
    WithClassName,
    useContainer,
    useSonata,
    useStore
} from '@/shared/lib/index.js';

import styles from './ChangeVolume.module.scss';

interface Props extends WithClassName {}

export const ChangeVolume = observer(({ className }: Props): ReactElement => {
    const { formatMessage } = useIntl();
    const container = useContainer();
    const sonata = useSonata();
    const { sonataState } = useStore();

    const storage = container.get(CONTAINER_STORAGE);

    const volume = storage.get(StorageKeys.YmPlayerVolume) ?? sonataState.volume ?? 1;

    const onChange = useCallback(
        (newVolume: number) => {
            sonata?.setVolume(newVolume);
            storage.set(StorageKeys.YmPlayerVolume, newVolume);
        },
        [sonata, storage]
    );

    const clickVolumeHandler = useCallback(() => {
        if (sonataState.volume !== 0) {
            storage.set(StorageKeys.YmPlayerPrevVolume, sonataState.volume);
            sonata?.setVolume(0);
            storage.set(StorageKeys.YmPlayerVolume, 0);
        } else {
            sonata?.setVolume(storage.get(StorageKeys.YmPlayerPrevVolume) ?? 1);
            storage.set(StorageKeys.YmPlayerVolume, sonataState.volume);
        }
    }, [storage, sonata, sonataState.volume]);

    const iconVariant = volume === 0 ? 'volumeOff' : 'volume';

    const buttonLabel =
        sonataState.volume === 0
            ? formatMessage({ id: 'player-actions.volume-on' })
            : formatMessage({ id: 'player-actions.volume-off' });

    return (
        <div className={clsx(styles.root, className)}>
            <Button
                radius="round"
                size="xxxs"
                variant="text"
                withRipple={false}
                onClick={clickVolumeHandler}
                aria-label={buttonLabel}
                icon={<Icon size="xs" className={styles.icon} variant={iconVariant} />}
                className={styles.button}
            />
            <Slider
                thumbSize="s"
                trackSize="s"
                value={volume}
                maxValue={1}
                step={0.01}
                onChange={onChange}
                aria-label={formatMessage({ id: 'player-actions.volume-control' })}
            />
        </div>
    );
});
