'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement } from 'react';

import { NavigationControls } from '@/features/navigation/index.js';

import { WithClassName } from '@/shared/lib/index.js';

import styles from './Header.module.scss';

interface Props extends WithClassName {
    isMobile?: boolean;
}

export const Header = observer(({ isMobile = false, className }: Props): ReactElement => {
    return (
        <header className={clsx(styles.root, className)}>
            <div className={styles.header}>
                <NavigationControls withForwardControl={!isMobile} />
            </div>
        </header>
    );
});
