'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { usePathname } from 'next/navigation.js';
import { ReactElement } from 'react';

import { Notification } from '@yandex-music-int/sol/components/Notification';

import { Content } from '@/widgets/content/index.js';
import { Navbar } from '@/widgets/navbar/index.js';
import { PlayerBar } from '@/widgets/player/index.js';
import { SomethingWentWrong } from '@/widgets/something-went-wrong/index.js';

import {
    CONTAINER_CONFIG_TOKEN,
    LayoutName,
    Platform,
    useContainer,
    useStore,
    WithChildren,
    NotificationContainerId
} from '@/shared/lib/index.js';
import { ErrorBoundary, DevPanel } from '@/shared/ui/index.js';

import styles from './DefaultLayout.module.scss';

interface Props extends WithChildren {}

export const DefaultLayout = observer(({ children }: Props): ReactElement => {
    const pathname = usePathname();
    const container = useContainer();
    const config = container.get(CONTAINER_CONFIG_TOKEN);

    const { settings } = useStore();

    const withPlayerBar = settings.layout === LayoutName.Mobile;
    const isMacOSApplication = settings.platform === Platform.MACOS;
    const isWindowsApplication = settings.platform === Platform.WINDOWS;

    return (
        <div className={clsx(styles.root, { [styles.root_applicationPreserveTitleBar]: isWindowsApplication })}>
            <Navbar
                className={clsx(styles.navbar, {
                    [styles.navbar_application_macos]: isMacOSApplication,
                    [styles.navbar_application_windows]: isWindowsApplication
                })}
            />
            <Content className={clsx(styles.content, { [styles.content_withPlayerBar]: withPlayerBar })}>
                <ErrorBoundary key={pathname} fallback={SomethingWentWrong}>
                    {children}
                    <Notification
                        className={styles.notificationInfoContainer}
                        enableMultiContainer
                        containerId={NotificationContainerId.INFO}
                        position="bottom-center"
                    />
                    <Notification
                        className={styles.notificationErrorContainer}
                        enableMultiContainer
                        containerId={NotificationContainerId.ERROR}
                        position="bottom-center"
                    />
                </ErrorBoundary>
            </Content>
            <PlayerBar className={styles.playerBar} />

            {config.dev.panel && <DevPanel />}
        </div>
    );
});
