'use client';

import { useCallback, useState } from 'react';
import { useIntl } from 'react-intl';

import { isVibeContext } from '@yandex-music-int/player-contexts';
import { RotorResourceSessionFeedback, UsersResourceLibraryResponse } from '@yandex-music-int/resource-collection';

import { NotificationDislike } from '@/entities/track/index.js';

import { NotificationContainerId, useNotify, useSonata, useStore } from '@/shared/lib/index.js';
import { BaseNotificationError } from '@/shared/ui/index.js';

import { MusicEntityMetaModel } from '../model/MusicEntityMeta.js';

export const useOnDislikeClick = (entityMeta: MusicEntityMetaModel | null): (() => Promise<void>) => {
    const { user, sonataState } = useStore();
    const sonata = useSonata();
    const { notify } = useNotify();
    const [pendingLibraryAction, setPendingLibraryAction] = useState(false);
    const { formatMessage } = useIntl();

    return useCallback(async () => {
        if (entityMeta) {
            if (!user.isAuthorized) {
                notify(
                    <BaseNotificationError
                        error={formatMessage({ id: 'authorization-messages.need-to-authorizate' })}
                    />,
                    {
                        containerId: NotificationContainerId.ERROR
                    }
                );
                return;
            }

            if (pendingLibraryAction) {
                return;
            }

            setPendingLibraryAction(true);

            const result: UsersResourceLibraryResponse = await entityMeta.toggleDislike();

            setPendingLibraryAction(false);

            if (result === UsersResourceLibraryResponse.OK) {
                notify(
                    <NotificationDislike
                        isDisliked={entityMeta.isDisliked}
                        coverUri={entityMeta.coverUri}
                        title={entityMeta.title}
                    />,
                    { containerId: NotificationContainerId.INFO }
                );

                const feedbackType = entityMeta.isDisliked
                    ? RotorResourceSessionFeedback.DISLIKE
                    : RotorResourceSessionFeedback.UNDISLIKE;

                if (sonata && isVibeContext(sonata.state.currentContext.value)) {
                    // в данном случае ждем отправки фидбека, чтобы рекомендации успели его обработать и перестроиться к моменту нашего скипа вперед
                    await sonata.state.currentContext.value.sendFeedback(
                        sonata.state.playerState,
                        sonata.state.queueState,
                        feedbackType
                    );
                }

                if (entityMeta.isDisliked && sonataState.canMoveForward) {
                    sonata?.moveForward();
                }
            } else {
                notify(<BaseNotificationError error={formatMessage({ id: 'error-messages.error-during-action' })} />, {
                    containerId: NotificationContainerId.ERROR
                });
            }
        }
    }, [entityMeta, user.isAuthorized, pendingLibraryAction, notify, formatMessage, sonataState, sonata]);
};
