'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useCallback } from 'react';
import { FormattedMessage } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Heading, Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '@/shared/lib/index.js';

import styles from './SomethingWentWrong.module.scss';

interface Props extends WithClassName {}

export const SomethingWentWrong = observer(({ className }: Props): ReactElement => {
    const handleRefreshButtonClick = useCallback(() => {
        window.location.reload();
    }, []);

    return (
        <div className={clsx(styles.root, className)}>
            <Icon className={styles.icon} variant="attention" size="xxl" />

            <Heading className={clsx(styles.title, styles.important)} variant="h3" size="xs">
                <FormattedMessage id="error-messages.something-went-wrong" />
            </Heading>

            <Caption
                className={clsx(styles.text, styles.important)}
                variant="span"
                type="text"
                size="l"
                weight="normal"
            >
                {process.env.BUILD_TYPE === 'web' && <FormattedMessage id="page-error.try-to-reload-page" />}

                {process.env.BUILD_TYPE === 'desktop' && <FormattedMessage id="page-error.try-to-restart-app" />}
            </Caption>

            <Button
                onClick={handleRefreshButtonClick}
                className={styles.button}
                role="link"
                color="secondary"
                size="l"
                radius="xxxl"
            >
                <Caption type="controls" variant="span" size="m">
                    {process.env.BUILD_TYPE === 'web' && <FormattedMessage id="page-error.reload-page-button" />}

                    {process.env.BUILD_TYPE === 'desktop' && <FormattedMessage id="page-error.restart-app-button" />}
                </Caption>
            </Button>
        </div>
    );
});
