import { ReactElement } from 'react';

import { PlayButton } from '@/shared/ui/PlayButton/PlayButton.js';

interface Props {
    disabled?: boolean;
    isPlaying: boolean;
    onClickPlayPause: () => void;
}

export const SonataControlsMobile = ({ disabled, isPlaying, onClickPlayPause }: Props): ReactElement => {
    return <PlayButton size="s" disabled={disabled} isPlaying={isPlaying} onClick={onClickPlayPause} />;
};
