'use client';

import { observer } from 'mobx-react-lite';
import { useCallback, useMemo, ReactElement } from 'react';

import { AudioPlayerStatus } from '@yandex-music-int/sonata';

import { AnalyticsBlock, AnalyticsPage, usePlayableVibeContext, useSonata, useStore } from '@/shared/lib/index.js';

import { MusicEntityMetaModel } from '../../model/MusicEntityMeta.js';

import { SonataControlsDesktop } from './SonataControlsDesktop/SonataControlsDesktop.js';
import { SonataControlsMobile } from './SonataControlsMobile/SonataControlsMobile.js';

interface Props {
    isMobile: boolean;
    entityMeta: MusicEntityMetaModel | null;
}

export const SonataControls = observer(({ isMobile, entityMeta }: Props): ReactElement => {
    const { sonataState, vibe } = useStore();
    const sonata = useSonata();

    const { togglePlay: toggleVibePlay } = usePlayableVibeContext({
        seeds: vibe.meta?.seeds,
        pageIdForFrom: AnalyticsPage.HOME,
        blockIdForFrom: AnalyticsBlock.RUP_MAIN
    });

    const isPlaying = useMemo(() => {
        return sonataState.status === AudioPlayerStatus.PLAYING;
    }, [sonataState.status]);

    const handlePlayPauseClick = useCallback(() => {
        if (entityMeta) {
            sonata?.togglePause();
        } else {
            toggleVibePlay();
        }
    }, [entityMeta, sonata, toggleVibePlay]);

    const handleNextTrackClick = useCallback(() => {
        sonata?.moveForward();
    }, [sonata]);

    const handlePrevTrackClick = useCallback(() => {
        sonata?.moveBackward();
    }, [sonata]);

    const SonataControlsContainer = useMemo(() => {
        return isMobile ? SonataControlsMobile : SonataControlsDesktop;
    }, [isMobile]);

    return (
        <SonataControlsContainer
            disabled={false}
            isPlaying={isPlaying}
            canMoveBackward={sonataState.canMoveBackward}
            canMoveForward={sonataState.canMoveForward}
            onClickPlayPause={handlePlayPauseClick}
            onClickNext={handleNextTrackClick}
            onClickPrev={handlePrevTrackClick}
        />
    );
});
