'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo } from 'react';

import { LayoutName, WithClassName, useStore } from '@/shared/lib/index.js';

import styles from './PlayerBar.module.scss';
import { PlayerBarDesktop } from './PlayerBarDesktop/PlayerBarDesktop.js';
import { PlayerBarMobile } from './PlayerBarMobile/PlayerBarMobile.js';

interface Props extends WithClassName {}

export const PlayerBar = observer(({ className }: Props): ReactElement => {
    const { settings, sonataState } = useStore();

    const PlayerBarContainer = useMemo(
        () => (settings.layout === LayoutName.Mobile ? PlayerBarMobile : PlayerBarDesktop),
        [settings.layout]
    );

    return <PlayerBarContainer entityMeta={sonataState.entityMeta} className={clsx(className, styles.root)} />;
});
